.gpt3-cta {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.gpt3-cta-content p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    font-weight: 500;
    color: #0E0E0E;
}
.gpt3-cta-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000;
}
.gpt3-cta-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}
.gpt3-cta-button button {
    background: #000;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border-radius: 40px;
    border: none;
    cursor: pointer;
    min-width: 150px;
}
@media screen and (max-width: 650px) {
    .gpt3-cta {
        flex-direction: column;
    }
    .gpt3-cta-button {
        margin-top: 2rem;
    }
}
@media screen and (max-width: 550px) {
    .gpt3-cta {
        margin: 4rem 2rem;
    }
    .gpt3-cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }
    .gpt3-cta-button button {
        font-size: 14px;
        line-height: 28px;
    }
}