.gpt3-footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}
.gpt3-footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}
.gpt3-footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}
.gpt3-footer-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}
.gpt3-footer-button p {
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 20px;
    color: #fff;
    word-spacing: 2px;
}
.gpt3-footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
}
.gpt3-footer-links div {
    width: 250px;
    margin: 1rem;
}
.gpt3-footer-links-logo {
    display: flex;
    flex-direction: column;
}
.gpt3-footer-links-logo img {
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;
}
.gpt3-footer-links-logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
}
.gpt3-footer-links-div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}
.gpt3-footer-links-div h4 {
    font-size: 14px;
    line-height: 16px;
    font-family: var(--font-family);
    color: #fff;
    margin-bottom: 1rem;
}
.gpt3-footer-links-div p {
    font-size: 12px;
    line-height: 14px;
    font-family: var(--font-family);
    color: #fff;
    margin-block: 0.5rem;
    cursor: pointer;
}
.gpt3-footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}
.gpt3-footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 14px;
    color: #fff;
}
@media screen and (max-width: 850px) {
    .gpt3-footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}
@media screen and (max-width: 550px) {
    .gpt3-footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
    .gpt3-footer-links div {
        margin: 1rem 0px;
    }
    .gpt3-footer-button p {
        font-size: 14px;
        line-height: 20px; 
    }
}
@media screen and (max-width: 400px) {
    .gpt3-footer-heading h1 {
        font-size: 26px;
        line-height: 38px;
    }
}