.gpt3-blog {
    display: flex;
    flex-direction: column;    
}
.gpt3-blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}
.gpt3-blog-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
    letter-spacing: -0.04em;
}
.gpt3-blog-container {
    display: flex;
    flex-direction: row;
}
.gpt3-blog-container-groupA {
    flex: 0.75;
    margin-right: 2rem;
}
.gpt3-blog-container-groupB {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}
@media screen and (max-width: 990px) {
    .gpt3-blog-container {
        flex-direction: column-reverse;
    }
    .gpt3-blog-container-groupA {
        margin: 2rem 0px;
    }
    .gpt3-blog-container-groupA .gpt3-blog-container-article  {
       width: 50%;
    }
    .gpt3-blog-container-groupA .gpt3-blog-container-article-image {
        height: 250px;
    }
}
@media screen and (max-width: 700px) {
    .gpt3-blog-container-groupB {
        grid-template-columns: repeat(1, 1fr);
    }
    .gpt3-blog-container-groupA .gpt3-blog-container-article  {
       width: 100%;
    }
    .gpt3-blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}
@media screen and (max-width: 550px) {
    .gpt3-blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
}